input[type="checkbox"] {
  width: 2.6em;
  height: 2.6em;
  appearance: none;
  border: 2px solid #D9624C;
  border-radius: 3px;
  vertical-align: middle;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg version='1.1' id='Layer_1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' x='0px' y='0px' width='512px' height='512px' viewBox='0 0 512 512' style='enable-background:new 0 0 512 512;fill: %2380080' xml:space='preserve'%3e%3cpath d='M448,71.9c-17.3-13.4-41.5-9.3-54.1,9.1L214,344.2l-99.1-107.3c-14.6-16.6-39.1-17.4-54.7-1.8 c-15.6,15.5-16.4,41.6-1.7,58.1c0,0,120.4,133.6,137.7,147c17.3,13.4,41.5,9.3,54.1-9.1l206.3-301.7 C469.2,110.9,465.3,85.2,448,71.9z'/%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  background-size: 0 0;
  transition: 0.1s ease;
}

input[type="checkbox"]:active {
  background-color: #ddd;
}

input[type="checkbox"]:checked {
  background-size: 100% 100%;
}

input[type="number"] {
  font-family: "Noto Sans", sans-serif;
  font-weight: 400;
  font-size: 0.5em;
}

body {
  height: 100vh;
}

.page__header {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 40px;
  font-size: 1.8rem;
}

.title--main {
  margin: 0 15px;
  font-family: "Noto Serif", serif;
  padding: 0 18px;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.title--sub {
  padding: 15px;
  margin-bottom: 20px;
  font-family: "Noto Serif", serif;
  font-size: 2.3rem;
}

.btn {
  display: flex;
  padding: 10px;
  font-size: 0.9rem;
}

.btn--check:hover,.btn--uncheck:hover {
  transition: all 0.1s ease-in;
}

.btn--check:hover:active,.btn--uncheck:hover:active {
  transform: translateY(4px);
  border-bottom-width: 2px;
  box-shadow: none;
}

.btn--check {
  background: #448883;
  box-shadow: 0 5px 0px #41817c;
  border-bottom: 2px solid #3c7773;
}

.btn--check:hover {
  background: #38706c;
  box-shadow: 0 4px 1px #38706c;
  border-bottom: 2px solid #356966;
}

.btn--uncheck {
  background: #D9624C;
  box-shadow: 0 5px 0px #d75b44;
  border-bottom: 2px solid #d55037;
}

.btn--uncheck:hover {
  background: #d3482f;
  box-shadow: 0 4px 1px #d3482f;
  border-bottom: 2px solid #cc442b;
}

.container-lists {
  color: #448883;
  font-size: 2.1rem;
}

.form {
  display: flex;
  justify-content: center;
  border: 2px solid #D9624C;
  max-width: 750px;
  margin: 30px auto;
  padding: 15px;
}

.ingredients-list {
  display: flex;
  flex-direction: column;
  list-style-type: none;
  font-family: "Noto Sans", sans-serif;
}

.ingredients-list__item {
  margin: 20px;
  padding-bottom: 25px;
  border-bottom: 1px dotted #448883;
}

.ingredients-list__price {
  margin-left: 100px;
}

.all-input-wrapper {
  display: flex;
  justify-content: space-around;
}

.input-number-wrapper {
  margin: 15px;
  position: relative;
}

.list--input__number {
  position: absolute;
  left: 25px;
  width: 4.6em;
  border: 2px solid #D9624C;
  border-radius: 3px;
}

.costs-cointainer {
  display: flex;
}

.costs-list {
  list-style: none;
  margin: auto;
  padding-bottom: 50px;
  font-size: 2.1rem;
}
